<template>
  <!-- Desktop -->
  <b-row class="card-project" v-if="windowWidth >= 768">
    <b-col
      class="image-project"
      :id="'image-project-' + title"
      cols="3"
      @click.prevent="redirect"
      @mouseover="hover"
      @mouseleave="restore_hover"
    >
      <imageComponent :width="windowWidth" :image_path="image" />
    </b-col>
    <b-col>
      <!--Takes the rest of the columns-->
      <div
        class="title-project"
        :id="'title-project-' + title"
        @click.prevent="redirect"
        @mouseover="hover"
        @mouseleave="restore_hover"
      >
        {{ title }}
      </div>
      <vue-custom-scrollbar suppressScrollX="true">
        <div class="description-project" v-html="description"></div>
      </vue-custom-scrollbar>
      <div class="footer-project">
        <div class="keywords">
          {{ keywords }}
        </div>
        <div class="date">
          {{ date }}
        </div>
      </div>
    </b-col>
  </b-row>

  <!-- Mobile -->
  <b-row class="card-project-mobile" v-else>
    <b-col>
      <b-row
        class="image-project-mobile"
        :id="'image-project-mobile-' + title"
        @click.prevent="redirect"
        @mouseover="hover"
        @mouseleave="restore_hover"
      >
        <imageComponent :width="windowWidth" :image_path="image" />
      </b-row>
      <div
        class="title-project-mobile"
        :id="'title-project-mobile-' + title"
        @click.prevent="redirect"
        @mouseover="hover"
        @mouseleave="restore_hover"
      >
        {{ title }}
      </div>
      <vue-custom-scrollbar suppressScrollX="true">
        <div class="description-project-mobile" v-html="description"></div>
      </vue-custom-scrollbar>
      <div class="footer-project-mobile">
        <div class="keywords">
          {{ keywords }}
        </div>
        <div class="date">
          {{ date }}
        </div>
      </div>
    </b-col>
  </b-row>
</template>

<script>
import ImageComponent from "@/components/ImageComponent.vue";
import vueCustomScrollbar from "vue-custom-scrollbar";

export default {
  name: "CardProject",
  components: {
    imageComponent: ImageComponent,
    vueCustomScrollbar
  },
  props: [
    "title",
    "description",
    "keywords",
    "date",
    "image",
    "url",
    "windowWidth"
  ],
  methods: {
    redirect() {
      window.open(this.url);
    },
    // TODO: Improve hovering logic
    hover() {
      if (this.windowWidth >= 768) {
        document.getElementById("image-project-" + this.title).style.opacity = 0.9;
        document.getElementById("title-project-" + this.title).style.color = "#4f5b62";
        document.getElementById("image-project-" + this.title).style.cursor = "pointer";
        document.getElementById("title-project-" + this.title).style.cursor = "pointer";
      } else {
        document.getElementById("image-project-mobile-" + this.title).style.opacity = 0.9;
        document.getElementById("title-project-mobile-" + this.title).style.color = "#4f5b62";
        document.getElementById("image-project-mobile-" + this.title).style.cursor = "pointer";
        document.getElementById("title-project-mobile-" + this.title).style.cursor = "pointer";
      }
    },
    restore_hover() {
      if (this.windowWidth >= 768) {
        document.getElementById("image-project-" + this.title).style.opacity = 1;
        document.getElementById("title-project-" + this.title).style.color = "#212529";
      } else {
        document.getElementById("image-project-mobile-" + this.title).style.opacity = 1;
        document.getElementById("title-project-mobile-" + this.title).style.color = "#212529";
      }
    }
  }
};
</script>

<style lang="scss">
.card-project {
  height: 300px;
  min-width: 450px;
  max-width: 1400px;
  margin: 16px 32px 16px 32px;
  background: #eceff1;
  border-radius: 10px;
  overflow: hidden;
}

.card-project-mobile {
  @extend .card-project;
  height: 600px;
}

// Image related
.image-project {
  overflow: hidden;
}

.image-project-mobile {
  @extend .image-project;
  height: 250px;
}

// Title related
.title-project {
  font-weight: bold;
  padding-left: 8px;
  font-size: 2.5em;
  height: 70px;
}

.title-project-mobile {
  @extend .title-project;
  font-size: 2em;
  height: 50px;
}

// Description related
.description-project {
  margin: 0;
  padding-left: 8px;
  padding-right: 8px;
  font-size: 1.2em;
  width: 100%;
  height: 205px; // 300 - 70 (title) - 25 (footer)
}

.description-project-mobile {
  @extend .description-project;
  font-size: 1em;
  height: 275px; // 600 - 250 (image) - 50 (title) - 20 (padding) - 3 (magic?)
}

// Footer related
.footer-project {
  padding-left: 8px;
  padding-right: 8px;
  display: flex; // Have two items next to each other
  height: 25px;
  align-self: center;
  background: #aab8bf;
}

.footer-project-mobile {
  @extend .footer-project;
}

.full {
  margin: 0;
  font-size: 0.9em;
}

.keywords {
  text-align: left;
  width: 80%;
}

.date {
  text-align: right;
  width: 20%;
}
</style>
