<template>
  <scrollbar suppressScrollX="true">
    <div
      class="container-fluid"
      :style="'max-width: 1400px; height: ' + windowHeight + 'px;'"
    >
      <card
        v-for="info in projectsInfo"
        :key="info.title"
        :title="info.title"
        :description="info.text"
        :keywords="info.keywords"
        :date="info.date"
        :image="info.image"
        :url="info.url"
        :windowWidth="windowWidth"
      ></card>
    </div>
  </scrollbar>
</template>

<script>
// @ is an alias to /src
import CardProject from "@/components/CardProject.vue";
import vueCustomScrollbar from "vue-custom-scrollbar";
import { mapGetters } from "vuex";

export default {
  name: "Projects",
  components: {
    card: CardProject,
    scrollbar: vueCustomScrollbar
  },
  computed: {
    ...mapGetters(["projectsInfo"])
  },
  data() {
    return {
      windowWidth: window.innerWidth,
      windowHeight: window.innerHeight - 72 // Honestly no idea why the offset
    };
  },
  mounted() {
    this.fetchData();
    window.addEventListener("resize", () => {
      this.windowWidth = window.innerWidth;
      this.windowHeight = window.innerHeight - 72; // Honestly no idea why the offset
    });
  },
  methods: {
    async fetchData() {
      await this.$store.dispatch("fetchProjects");
    }
  }
};
</script>
